<template>
  <div class="search-main">
    <div class="search-box">
      <div class="search-type">
        {{selectType === 1 ? '仅标题' : '标题+正文'}}
        <span class="el-icon-sort" @click="changeType"></span> 
      </div>
      <div class="keyword-box">
        <el-input
          placeholder="请输入搜索词"
          v-model="keyword"
          class="input-with-select"
        >
          <el-button slot="append" icon="el-icon-search" :loading="searchLoading" @click="search"></el-button>
        </el-input>
      </div>
    </div>
    <div class="list-tip" v-show="showTip">共有<span>{{resultSum}}</span>条检索结果</div>
    <div class="list-box" v-show="resultSum>0">
      <ul>
        <li v-for="(item, index) in dataArr" :key="index">
          <div class="title" v-html="item.title" @click="showDetail(item.id, item.point)"></div>
          <div class="item" v-for="(itema, indexa) in item.matchArr.slice(0,3)" :key="indexa" v-html="itema.title" @click="showDetail(item.id, itema.point)">
          </div>
          <div class="show-more" v-if="item.showMore&&item.matchArr.length>3" @click="item.showMore = !item.showMore">更多</div>
          <div class="item-box" v-if="!item.showMore">
            <div class="item" v-for="(itema, indexa) in item.matchArr.slice(3,item.matchArr.length)" :key="indexa" v-html="itema.title" @click="showDetail(item.id, itema.point)">
          </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchIndex',
  components: {},
  data() {
    return {
      keyword: '',
      keywordOld: '',
      selectType: 1,
      searchLoading: false,
      resultSum: 0,
      showTip: false,
      dataArr: [
        // {
        //   id: '1111',
        //   title: '法律文件一法律文件一法律文件一法律文件一法律文件一法律文件一法律文件一<i class="el-icon-arrow-right"></i>',
        //   point: '',
        //   showMore: true,
        //   matchArr: [
        //     {
        //       title: '第一章第一条第一章第一条第一章第一条第一章第一条第一章第一条<i class="el-icon-arrow-right"></i>',
        //       ponit: '0-1',
        //       des: '法律法律法律法律<span>法律法律法律法律</span>'
        //     },
        //     {
        //       title: '第一章第一条',
        //       ponit: '0-1',
        //       des: '法律法律法律法律<span>法律法律法律法律</span>'
        //     },
        //     {
        //       title: '第一章第一条',
        //       ponit: '0-1',
        //       des: '法律法律法律法律<span>法律法律法律法律</span>'
        //     },
        //     {
        //       title: '第一章第一条',
        //       ponit: '0-1',
        //       des: '法律法律法律法律<span>法律法律法律法律</span>'
        //     },
        //     {
        //       title: '第一章第一条',
        //       ponit: '0-1',
        //       des: '法律法律法律法律<span>法律法律法律法律</span>'
        //     }
        //   ]
        // }
      ]
    };
  },
  mounted() {},
  methods: {
    changeType() {
      if (this.selectType === 1) {
        this.selectType = 2
      } else {
        this.selectType = 1
      }
    },
    search() {
      if (this.keyword.trim() === '') {
        alert('请输入关键词')
        return
      }

      if (this.keyword.length < 2) {
        alert('关键词不能少于两个字')
        return
      }

      this.searchLoading = true
      this.$axios({
        method: 'post',
        url: '/api/law/search',
        data: {
          type: this.selectType,
          keyword: this.keyword
        }
      }).then(res => {
        this.searchLoading = false
        const resData = res.data
        this.resultSum = resData.length
        if (this.resultSum > 0) {
          const tempArr = []
          this.keywordOld = this.keyword
          if (this.selectType === 1) {
            for (const m in resData) {
              const tempObj = {}
              tempObj.id = resData[m]._id
              tempObj.title = resData[m].title.replace(new RegExp(this.keyword, 'g'), `<span>${this.keyword}</span>`) + '<i class="el-icon-arrow-right"></i>'
              tempObj.point = ''
              tempObj.showMore = true
              tempObj.matchArr = []
              tempArr.push(tempObj)
            }
          } else {
            for (const m in resData) {
              const tempObj = {}
              tempObj.id = resData[m]._id
              tempObj.title = resData[m].title.replace(new RegExp(this.keyword, 'g'), `<span>${this.keyword}</span>`) + '<i class="el-icon-arrow-right"></i>'
              tempObj.point = ''
              tempObj.showMore = true
              tempObj.matchArr = []
              for (const n in resData[m].content) {
                let title = resData[m].content[n].partTitle
                let point = String(n)
                if (resData[m].content[n].partDes.indexOf(this.keyword) > -1) {
                  resData[m].content[n].partDes.replace(new RegExp(this.keyword, 'g'), `<span>${this.keyword}</span>`)
                  tempObj.matchArr.push({
                    title: title + ' ' +resData[m].content[n].partDes + '<i class="el-icon-arrow-right"></i>',
                    point: point
                  })
                } else {
                  for (const j in resData[m].content[n].partContent) {
                    let pTitle = title + resData[m].content[n].partContent[j].chapterTitle
                    let pPoint = point + '-' + String(j)
                    if (resData[m].content[n].partContent[j].chapterDes.indexOf(this.keyword) > -1) {
                      resData[m].content[n].partContent[j].chapterDes.replace(new RegExp(this.keyword, 'g'), `<span>${this.keyword}</span>`)
                      tempObj.matchArr.push({
                        title: pTitle + ' ' +resData[m].content[n].partContent[j].chapterDes + '<i class="el-icon-arrow-right"></i>',
                        point: pPoint
                      })
                    } else {
                      for (const k in resData[m].content[n].partContent[j].chapterContent) {
                        const newTitle = pTitle + resData[m].content[n].partContent[j].chapterContent[k].articleTitle
                        let newPoint = pPoint + '-' + String(k)
                        // 截取前先替换掉原有的换行符
                        let tempTitle = resData[m].content[n].partContent[j].chapterContent[k].articleContent.replace(new RegExp('<br>', 'g'), '')
                        const findIndex = tempTitle.indexOf(this.keyword)
                        if (findIndex > -1) {
                          // 截取关键词前后五个字符
                          const start = findIndex >= 5 ? findIndex - 5 : 0
                          const end = findIndex + this.keyword.length + 5
                          tempTitle = tempTitle.substring(start, end)
                          tempTitle = tempTitle.replace(new RegExp(this.keyword, 'g'), `<span>${this.keyword}</span>`)
                          tempObj.matchArr.push({
                            title: newTitle + ' ' +tempTitle + '<i class="el-icon-arrow-right"></i>',
                            point: newPoint
                          }) 
                        }
                      }
                    }
                  }
                }
                
              }
              tempArr.push(tempObj)
            }
          }
  
          this.dataArr = tempArr
        }
        this.showTip = true
      }).catch(err => {
        console.log(err)
        this.searchLoading = false
      })
    },
    showDetail(id, point) {
      this.$router.push({ name: 'SearchDetail', query: { id,  point, keyword: this.keywordOld}})
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss">
.search-main {
  margin: 0 auto;
  background-color: #e5f1ff;
  position: absolute;
  width: 100%;
  min-height: 100%;
  left: 0;
  top: 0;
  .search-box {
    height: 70px;
    background-color: #3460ab;
    overflow: hidden;
    .search-type {
      width: 125px;
      float: left;
      height: 100px;
      line-height: 70px;
      color: #ffffff;
      padding-left: 15px;
      position: relative;
      &>span {
        position: absolute;
        width: 20px;
        height: 20px;
        line-height: 20px;
        top: 25px;
        right:10px;
        transform:rotate(90deg);
      }
    }
    .keyword-box {
      width: calc(100% - 125px);
      float: left;
      height: 70px;
      padding-top: 15px;
      padding-right: 15px;
      .input-with-select {
        width: 100%;
        .el-icon-search {
          font-size: 20px;
        }
      }
    }
  }
  .list-tip {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px dashed #dde0e4;
    margin:0 15px;
    text-align: left;
    color: #2c59a5;
    & > span {
      color: #ff5635;
      margin: 0 5px;
    }
  }
  .list-box {
    margin: 0 15px;
    & > ul {
      & > li {
        padding: 20px 0 10px 0;
        border-bottom: 1px dashed #dde0e4;
        .title {
          margin-bottom: 10px;
          color: #2c2b2d;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
          position: relative;
          padding-right: 20px;
          i {
            position: absolute;
            width: 18px;
            height: 18px;
            font-size: 18px;
            line-height: 18px;
            top: 0;
            right: 0;
          }
        }
        .item {
          font-size: 14px;
          color: #888888;
          margin-bottom: 10px;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
          position: relative;
          padding-right: 20px;
          i {
            position: absolute;
            width: 16px;
            height: 16px;
            font-size: 16px;
            line-height: 16px;
            top: 0;
            right: 0;
          }
        }
        span {
          color: #ff5635;
        }
        .show-more {
          color: #2c59a5;
          font-size: 14px;
        }
      }
    }
  }
  .page-box {
    margin-top: 20px;
    padding: 0 20px;
  }
}
</style>
